
a {
  color:black;
  text-decoration: none;
}
h1 {
  font-size: 35pt;
  font-weight: 900;
  margin: 0;
  font-family: AleoBold;
  margin-bottom: 2rem;
}

h2 {
  font-size: 35pt;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-family: AleoRegular;
  letter-spacing: -1px;
}
h3 {
  font-family: AvenirBold;
  margin: 0;
  font-size: 20pt;
}

h4 {
  margin: 0;
  font-size: 18pt;
  font-family:AleoLight;
}

html {
  font-size: 14pt;
}



.move{
  width:3rem;
}


.bg img {
  position: absolute;
  z-index: -9;
  bottom: -2rem;
  right: 0;
  width: 80vh;
  height: auto;
}

.crystals {
  position: absolute;
  top: 8rem;
  left: 0;
  height: calc(100vh - 8rem);
  width: 100vw;
  min-width:1000px;
}

