.Modal {
  position: absolute;
  top: 2.5vh;
  left: 2.5vh;
  width: calc(100vw - 5vh);
  height: 95vh;
  z-index: 10;
  border-radius: 10px;
  background-color: #f5edee;
  display: flex;
  flex-direction: column;
  animation: slideUp .3s ease-out;
}

@keyframes slideUp {
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateY(0%);
  }
}

.modalHeader {
  border-top: 1px solid black;
  border-left: 1px solid black;
  border-right: 1px solid black;

  border-radius: 7px 7px 0 0;
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  align-items: center;
  background-color: #eac1d6;
  font-size: 22pt;
  font-weight: 700;
}

.close {
  cursor: pointer;
  width: 34px;
  height: 34px;
}

.modalContainer {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-height: 0;
  padding: 1rem;
  overflow: auto;
  gap: 1rem;
  padding: 1rem 7rem;
  border: 1px solid black;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.modalContainer img {
  border-radius: 10px;
  border: 1px solid black;
}

.modalContainer h1 {
  margin-bottom: 1rem;
}
